import React from "react";
import ReactDOM from "react-dom";
import classnames from "classnames";
import "../../css/boxart.scss";

class BoxArt extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            flipped: props.defaultFlipped
        };
    }

    handleFlip = (event) => {
        this.setState({ flipped: !this.state.flipped });
        
        if (this.props.onFlip) {
            this.props.onFlip(event);
        }
    };

    render = () => {
        const {
            flipped = this.state.flipped
        } = this.props;

        const contentClasses = classnames(
            "content",
            { "flipped": flipped }
        );

        return (
            <div className="box-art">
                <div className="header">
                    {this.props.header} 
                </div>
                <div className={contentClasses} onClick={this.handleFlip}>
                    <div className="content-face content-face-front"> 
                        {this.props.frontComponent}
                    </div>
                    <div className="content-face content-face-back">
                        {this.props.backComponent}
                    </div>
                </div>
                <div className="footer">
                    {this.props.footer}
                </div>
            </div>
        );
    };
}

BoxArt.defaultProps = {
    defaultFlipped: false,
    flipped: undefined,
    onFlip: undefined 
};

export default BoxArt;
