import React from "react";
import ReactDOM from "react-dom";
import classnames from "classnames";
import { CSSTransition } from 'react-transition-group';
import "../../css/slider.scss";

class Slider extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentIndex: props.defaultIndex
        };
    }

    setPrevItemIdx = () => {
        this.setState(prevState => {
            const dataLen = this.props.data.length;
            const currentIndex = dataLen == 0 ? 0 : (this.state.currentIndex - 1 + dataLen) % dataLen;
            return { currentIndex };
        });

        if (this.props.onPrevClick) {
            this.props.onPrevClick();
        }
    };

    setNextItemIdx = () => { 
        this.setState(prevState => {
            const dataLen = this.props.data.length;
            const currentIndex = dataLen == 0 ? 0 : (this.state.currentIndex + 1 + dataLen) % dataLen;
            return { currentIndex };
        });

        if (this.props.onNextClick) {
            this.props.onNextClick();
        }
    };

    render = () => {
        const {
            className,
            currentIndex = this.state.currentIndex,
            data
        } = this.props;

        const classes = classnames("slider", className);
        const leftArrowClasses = classnames(
            "slider-left-arrow",
            { "d-none": this.state.currentIndex == 0 }
        );
        const rightArrowClasses = classnames(
            "slider-right-arrow",
            { "d-none": data.length == 0 || this.state.currentIndex == data.length - 1 }
        );

        return (
            <div className={classes}>
                <div className="slider-header">
                    {this.props.header}
                </div>
                <div className="slider-content">
                    <div 
                        className={leftArrowClasses} 
                        onClick={this.setPrevItemIdx}
                    />
                    <CSSTransition
                        timeout={300}
                        classNames="slider-content"
                    >
                    {this.props.slideRender(data[this.state.currentIndex])}
                    </CSSTransition>
                    <div 
                        className={rightArrowClasses} 
                        onClick={this.setNextItemIdx}
                    />
                </div>
                <div className="slider-footer">
                    {this.props.footer}
                </div>
            </div>
        );
    };
}

Slider.defaultProps = {
    className: "",
    title: "",
    defaultIndex: 0,
    currentIndex: null,
    onPrevClick: null,
    onNextClick: null,
    slideRender: () => {},
    data: []
};

export default Slider;
