import React from "react";
import ReactDOM from "react-dom";
import moment from "moment";
import TableHead from "./components/table/TableHead";
import TableBody from "./components/table/TableBody";
import TableRow from "./components/table/TableRow";
import TableCell from "./components/table/TableCell";
import Slider from "./components/Slider";
import BoxArt from "./components/BoxArt";
import { fieldValue } from "./utils";

const renderTable = data => {
    return (
       <table className="box-art-table">
            <TableBody>
                {data.map((d, idx) => (
                    <TableRow key={idx}>
                        <TableCell head key={idx}>
                            {d.label}
                        </TableCell>
                        <TableCell key={idx+1}>
                            {d.value}
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </table>
    );
};

const slideRender = (config, data) => {
    return (
        <div className="box-art-group">
            { 
                [].concat(data).map((d, idx) => {
                    const boxArtUrl = d.releaseBoxArt || d.distLogo;
                    const boxArt = boxArtUrl && <img src={boxArtUrl} />;
                    const table = d.restData && renderTable(d.restData);

                    return (
                        <BoxArt
                            key={idx}
                            frontComponent={boxArt || table}
                            backComponent={boxArt && table}
                            footer={d.releaseDate}
                        />
                    );
                })
            }
        </div>
    );
};

const prepareData = (queryResults, config) => {
    let relDateDimension = {};
    let boxArtDimension = {};
    let distLogoDimension = {};
    const restDimensions = [];
     
    config.query_fields.dimensions.forEach(dimension => {
        if (dimension.name.match(/\.*release_date$/)) {
            relDateDimension = dimension;
        } else if (dimension.name.match(/\.*box_art_url$/)) {
            boxArtDimension = dimension;
        } else if (dimension.name.match(/\.*logo_url$/)) {
            distLogoDimension = dimension;
        } else {
            restDimensions.push(dimension);
        }
    });

    const data = queryResults.map((row, idx) => {
        const releaseDate = fieldValue(row, relDateDimension.name); 
        const releaseBoxArt = fieldValue(row, boxArtDimension.name); 
        const distLogo = fieldValue(row, distLogoDimension.name);

        const restData = restDimensions.map(dimension => {
            return {
                name: dimension.name,
                label: dimension.label_short,
                value: fieldValue(row, dimension.name)
            };
        });

        return {
            releaseDate,
            releaseBoxArt,
            distLogo,
            restData
        }; 
    });

    const dataChunks = [];
    while (data.length) {
        dataChunks.push(data.splice(0, 2));
    }

    return dataChunks;
};

looker.plugins.visualizations.add({
    create: function(element, config){
    },
    updateAsync: function(data, element, config, queryResponse, details, doneRendering){
        const preparedData = prepareData(data, config);

        ReactDOM.render(
            <Slider 
                data={preparedData}
                slideRender={slideRender.bind(undefined, config)}
            />, 
            element
        );
        
        doneRendering();
    }
});
